 /* Сайдбар */
.important {
     display: flex;
     flex-direction: column;
 }

 /* Сайдбар важное */
 .important {
     box-sizing: border-box;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     gap: 15px;
     padding: 20px;
     background: #FFFFFF;
     border: 1px solid #EEEEEE;
     box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
     border-radius: 20px;
     width: 360px;
     margin-bottom: 40px;
 }

 .important__price {
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     padding: 0px;
     gap: 10px;
 }

 .important-old {
     display: flex;
     align-items: center;
     padding: 0px;
     gap: 10px;
 }

 .important-old__prise {
     font-weight: 500;
     font-size: 16px;
     line-height: 16px;
     text-decoration-line: line-through;
     color: #888888;
 }

 .old-price__sale {
     align-items: center;
     gap: 10px;
     background: #F36223;
     border-radius: 500px;
 }

 .old-price__text {
     font-weight: 500;
     font-size: 16px;
     line-height: 16px;
     color: #FFFFFF;
     padding: 4px 8px;
 }

 .important__actual {
     font-weight: 500;
     font-size: 32px;
     line-height: 39px;
     color: #000000;
 }

 .important-delivery {
     box-sizing: border-box;
     display: flex;
     flex-direction: column;
     align-items: flex-start;
     gap: 10px;
     margin-bottom: 20px;
 }

 .important-delivery__text {
     font-weight: 400;
     font-size: 16px;
     line-height: 19px;
 }

 .important__wrapper {
     width: 100%;
     display: flex;
     justify-content: space-between;
     align-items: flex-start;
 }

 .bold-text {
     font-weight: bold;
 }

 .advertisement {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 10px;
}

 @media (min-width: 360px) and (max-width: 1023px) {
    .advertisement {
        display: none;
    }
}

.advertisement P {
    color: #888888;
}

.advertisement__iframes {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 15px;
}

.ads {
    background: #F0F0F0;
    border: 1px dashed #888888;
    height: 300px;
    width: 420px;
    box-sizing: border-box;
}

@media (max-width: 1500px) {
    .ads {
        width: 360px;
    }
}

